function metarate(sex, gew, groe, alt) {
    // Grundumsatz-Rechner - individueller Kalorienverbrauch


    let geschlecht = sex; // Man = false, Woman = true
    let gewicht_kg = gew;
    let groesse_cm = groe;
    let alter_jahre = alt;

    let grundumsatz = NaN;

    if (geschlecht == true) {
        grundumsatz = 66.47 + (13.75 * gewicht_kg) + (5 * groesse_cm) - (6.8 * alter_jahre);
    } else {
        grundumsatz = 655.1 + (9.56 * gewicht_kg) + (1.85 * groesse_cm) - (4.7 * alter_jahre);
    }



    // PAL - Fktor - Pysical Activity Level
    var pal = {
        Schlafen: 0.95,
        Liegen: 1.2,
        Sitzen: 1.5,
        SitzenStehen: 1.7,
        Gehen: 1.9,
        KÃ¶rperlich: 2.4
    };

    //console.log(pal.Sitzen);

    return grundumsatz * pal.SitzenStehen;
}


function age_kcal_calc(sex, gew, groe, alt){
    
    let max_age = 80;
    let age_kcal = [];
    let total_kcal = 0;

    for (let i = alt; i <= 80; i++ ){
        age_kcal.push(metarate(sex, gew, groe, i));
        total_kcal += metarate(sex, gew, groe, i);
    }

    // console.log(total_kcal);
    return total_kcal;
    
}


export { metarate, age_kcal_calc };