<template>
    <div class="search">
        <div class=" search">
            <autocomplete
                :search="search"
                placeholder="WÃ¤hle deine SÃ¼nde!"
                aria-label="WÃ¤hle deine SÃ¼nde!"
                :get-result-value="getResultValue"
                @submit="handleSubmit"
                >
            </autocomplete>
            
            <!-- <input class="btn" type="reset" value="Suche lÃ¶schen"> -->
            
        </div>
    </div>
</template>

<script>
import store from '../store/store';
import { mapState, mapMutations } from 'vuex';
import { metarate } from '../kcalcalc/fresstabelle';
import customSearch from './customSearch.vue';
import $ from 'jquery';

import Autocomplete from '@trevoreyre/autocomplete-vue';
import '@trevoreyre/autocomplete-vue/dist/style.css';

import * as d3 from 'd3';



export default {
    name: 'search_prod',
    components:{
        Autocomplete,
        customSearch
    },
    data: function(){
        return {
            data: this.$root.data,
            default_input: "Lieblings-SÃ¼Ãigkeit",
            focused: false,
            value: '',
            results: []
            }
    },
    computed: {
        ...mapState([
            'selection',
        ]),
        noResults() {
            return this.value && this.results.length === 0
        },
        // resultListeners(){
        //     console.log("ResultListListener");
        //     return "testresultvalue"
        // }
    },
    methods: {
        ...mapMutations([
            'set_selection',
        ]),
        search(input) {
      
            if (input.length < 1) {
                return [];
            }
            
            return this.data.filter(item => {
                return item.name.toLowerCase().startsWith(input.toLowerCase());
            })
            
            // Save value to vue store. set [0] cause results is an object in object
            // this.set_selection(this.results[0]);

        },     
        getKcal: function(result){
            this.set_selection(result);
        },
        getResultValue: function(result){
            // if(result.length > 0){
            //     result = "";
            // }
            // this.set_selection(result);
                        
            if ($('.downloadArea')){
                 $('.downloadArea').remove();
                 $('#chart').show();
            }

            return result.name + " mit " + result.kalorien + "/100g ";
        },
        clear_field: function(){
            return "";
        },
        handleSubmit: function(result){
            // console.log("Deine Grafik ist generiert!")
            // console.log("Result: " + result.name);
            $('.createGrafik').show();
           
            this.set_selection(result);
            $('.autocomplete-input').value = "";
        }
    }    
}
</script>

