<template>
        <div class="kcal">
                <div class="row firstinput">
                        <div class="column #geschlecht">
                        <label> Geschlecht </label>
                        <button type="button" class="button btn" :class="{ btnOff: add_geschlecht }" v-on:click="add_geschlecht=false">
                             <span> Frau </span> 
                        </button>
                        <button type="button" class="button btn" :class="{ btnOff: !add_geschlecht }" v-on:click="add_geschlecht=true">
                             <span> Mann </span> 
                        </button>
                    </div>
                    <div class="column #gewicht">
                            <label for="weightField"> Gewicht in kg </label>
                            <input type="text" id="weightField" v-model="add_gewicht">
                    </div>
                </div>
                <div class="row secondinput">
                    <div class="column #groesse">
                        <label for="heightField"> GrÃ¶Ãe in cm </label>
                        <input type="text" id="heightField" v-model="add_groesse">
                    </div>
                    <div class="column #alter">
                        <label for="agetField"> Alter in Jahren </label>
                        <input type="text" id="ageField" v-model="add_alter">
                    </div>
                </div>
        </div>
</template>

<script>
import store from '../store/store';
import { mapState, mapMutations } from 'vuex';
import { metarate, age_kcal_calc } from '../kcalcalc/fresstabelle';

export default {
    name: 'kcal',
    components: {
    },
    data(){
        return{
            add_geschlecht: false,
            add_gewicht: 65,
            add_groesse: 170,
            add_alter: 40,
            add_grundumsatz: 0,
            add_total_kcal:0
        }
    },
    beforeMount() {
        this.kcalCalc;
        this.age_calc;
    },
    watch: {
        add_geschlecht: function(){
            this.kcalCalc;
            this.age_calc;
        },
        add_gewicht: function(){
            this.kcalCalc;
            this.age_calc;
        },        
        add_groesse: function(){
            this.kcalCalc;
            this.age_calc;
        },
        add_alter: function(){
            this.kcalCalc;
            this.age_calc;
        },
    },
    computed: {
        ...mapState([
            'geschlecht', 
            'gewicht_kg',
            'groesse_cm',
            'alter_jahre',
            'grundumsatz'
        ]),
        kcalCalc: function(){
            this.add_grundumsatz = metarate(this.add_geschlecht, this.add_gewicht, this.add_groesse, this.add_alter)
            
            this.set_geschlecht(this.add_geschlecht);
            this.set_gewicht(this.add_gewicht);
            this.set_groesse(this.add_groesse);
            this.set_alter(this.add_alter);
            this.set_grundumsatz(this.add_grundumsatz);
            // console.log("VUEXTEST" + this.geschlecht);
            return this.add_grundumsatz;
        },
        age_calc: function(){
            let total_kcal = age_kcal_calc(this.add_geschlecht, this.add_gewicht, this.add_groesse, this.add_alter);
            this.set_total_kcal(total_kcal);
        },
    },
    methods: {
        ...mapMutations([
            'set_geschlecht',
            'set_gewicht',
            'set_groesse',
            'set_alter',
            'set_grundumsatz',
            'set_total_kcal'
        ]),

        
    },

}

</script>

