import Vue from 'vue';
import App from './App.vue';
import store from './store/store.js';
import VueMeta from 'vue-meta';
import VueAnalytics from 'vue-analytics';


Vue.use(VueMeta);
Vue.use(VueAnalytics, {
  id: 'UA-150516970-1' 
});

let data = require('dsv-loader?delimiter=,s!./data/Data.csv');

new Vue({
    mode: 'developement',
    data: {
        data: data,
    },
    metaInfo: () => ({
        titleTemplate: '%s | Zu dick? Rechne ab!',
        htmlAttrs: {
          lang: 'de',
          amp: undefined
        },
        __dangerouslyDisableSanitizers: ['script']
      }),
    store,
    render: h => h(App)
}).$mount('#app')

