        }
    },
    computed: {
        ...mapState([
            'geschlecht', 
            'gewicht_kg',
            'groesse_cm',
            'alter_jahre',
            'grundumsatz'
        ]),
        startRotation: function() {
            this.banner.timer = setInterval(this.nextBanner, 5000);
        }
    },
    methods: {
        ...mapMutations([
            'set_geschlecht',
            'set_gewicht',
            'set_groesse',
            'set_alter',
            'set_grundumsatz',
            'set_total_kcal'
        ]),
        stopRotation: function(){
            clearInterval(this.banner.timer);
            this.banner.timer = null;
        },
        prev: function(){
            // let targetId = event.currentTarget.id;
            this.stopRotation();
            this.nextBanner(-1);
        },
        next: function(){
            this.stopRotation();
            this.nextBanner(1);
        },
        add_classes: (item, index) => {
            
            return "class_" + index;
        },

        nextBanner: function(input) {
            
            // this.banner.bannerIndex +=1;

            // if (this.banner.bannerIndex == 3){
