            // }
            // console.log(this.banner.bannerIndex);
            if (input < 0){
                if (this.banner.bannerIndex > 0){
                    this.banner.bannerIndex -= 1;
                } else {
                    this.banner.bannerIndex = 2;
                }
            } else {
                if (this.banner.bannerIndex < 2){
                    this.banner.bannerIndex += 1;
                } else {
                    this.banner.bannerIndex = 0;       
                }
            }
            // console.log(this.banner.bannerIndex);
        },
            
},

}

