<template>
    <input
        class="clearable"
        placeholder="Suche nach deiner SÃ¼Ãigkeit"
        aria-label="Suche nach deiner SÃ¼Ãigkeit"
        style="padding: 24px 24px 24px 48px"
        :value="getResultValue(value)"
        v-on="$listeners"
      /> 
</template>

<script>
import $ from 'jquery';



// Add Clear x to the search field
function tog(v){return v?'addClass':'removeClass';} 
    $(document).on('input', '.clearable', function(){
        $(this)[tog(this.value)]('x');
    }).on('mousemove', '.x', function( e ){
        $(this)[tog(this.offsetWidth-18 < e.clientX-this.getBoundingClientRect().left)]('onX');
    }).on('touchstart click', '.onX', function( ev ){
        ev.preventDefault();
        $(this).removeClass('x onX').val('').change();
    });


export default {
   name: 'customSearch',
   props: {
      value: {

      }
    },
  computed: {
  },
  methods: {
    getResultValue(result) {
      if(result.length < 10){
        return result
      }
      return result.name
    },
  },

} </script>

